/* --------------------------------------------------------------
 * = reset 
 * ----------------------------------------------------------- */

/*  */
html {
    color: @text-color;
    background: @body-bg;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-rendering: optimizelegibility;
}

/*  IE9+ | Chrome | Firefox  <html>  .borderbox  class */
* {
    box-sizing: border-box;
}

/*  */
body, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p,
blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, menu, nav, section {
    margin: 0;
    padding: 0;
}

/*  HTML5 , IE  js  createElement(TAG) */
article, aside, details, figcaption, figure, footer, header, menu, nav, section {
    display: block;
}

/* HTML5  img  */
audio, canvas, video {
    display: inline-block;
}

/*  font  */
body, .pure-g [class*="pure-u"], button, input, select, textarea {
    font: 300 14px/1.8 PingFang SC, Lantinghei SC, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0;
}

/* Table cell  */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*  */
fieldset, img {
    border: 0;
}

/* / */
blockquote {
    position: relative;
    color: @gray;
    font-weight: 400;
    border-left: 1px solid @brand-primary;
    padding-left: 1em;
    margin: 1em 3em 1em 2em;
}

@media only screen and ( max-width: 48em ) {
    blockquote {
        margin: 1em 0;
    }
}

/* Firefox  */
acronym, abbr {
    border-bottom: 1px dotted;
    font-variant: normal;
}

/*  */
abbr {
    cursor: help;
}

/*  del  */
del {
    text-decoration: line-through;
}

address, caption, cite, code, dfn, em, th, var {
    font-style: normal;
    font-weight: 400;
}

/* , li  */
ul, ol {
    list-style: none;
}

/* ,  */
caption, th {
    text-align: left;
}

q:before, q:after {
    content: '';
}

/*  */
sub, sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
}

:root sub, :root sup {
    vertical-align: baseline; /* for ie9 and other modern browsers */
}

sup {
    top: -0.5em;
}

sub {
    bottom: -0.25em;
}

/*  hover  */
a {
    color: @link-color;
}

a:hover {
    text-decoration: underline;
}

.typo a:hover {
    color: @link-hover-color;
    text-decoration: none;
}

.typo a:hover {
    border-bottom-color: #555;
    color: #555;
    text-decoration: none;
}

/*  */
ins, a {
    text-decoration: none;
}

/*  u  html5 Draft
 *  class="typo-u" 
 *  <u> http://www.whatwg.org/specs/web-apps/current-work/multipage/text-level-semantics.html#the-u-element
 *  4 http://www.w3.org/TR/html401/appendix/changes.html#idx-deprecated
 *  <u> http://html5doctor.com/u-element/
 */
u, .typo-u {
    text-decoration: underline;
}

/*  */
mark {
    background: #fffdd1;
    border-bottom: 1px solid #ffedce;
    padding: 2px;
    margin: 0 5px;
}

/*  */
pre, code, pre tt {
    font-family: Menlo,Monaco,Consolas,"Courier New",monospace;
}

pre {
    background: #f8f8f8;
    border: 1px solid #ddd;
    padding: 1em 1.5em;
    display: block;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}

code{
    padding: 2px 4px;
    font-size: 90%;
    color: #c7254e;
    background-color: #f9f2f4;
    border-radius: 1px;
}

/*  horizontal rule */
hr {
    border: none;
    border-bottom: 1px solid #cfcfcf;
    margin-bottom: 0.8em;
    height: 10px;
}

/*  */
small, .typo-small,
figcaption {
    font-size: 0.9em;
    color: #888;
}

strong, b {
    font-weight: bold;
    color: @gray-dark;
}

/*  */
[draggable] {
    cursor: move;
}

.clearfix:before, .clearfix:after {
    content: "";
    display: table;
}

.clearfix:after {
    clear: both;
}

/*  */
.textwrap, .textwrap td, .textwrap th {
    word-wrap: break-word;
    word-break: break-all;
}

.textwrap-table {
    table-layout: fixed;
}

/*  serif : iOS  fallback  sans-serif */
.serif {
    font-family: Palatino, Optima, Georgia, serif;
}

/* / */
.typo p, .typo pre, .typo ul, .typo ol, .typo dl, .typo form, .typo hr, .typo table,
.typo-p, .typo-pre, .typo-ul, .typo-ol, .typo-dl, .typo-form, .typo-hr, .typo-table, blockquote {
    margin-bottom: 1.2em
}

h1, h2, h3, h4, h5, h6 {
    font-family: PingFang SC, Verdana, Helvetica Neue, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans-serif;
    color: @gray-dark;
    line-height: 1.35;
}

/* margin  */
.typo h1, .typo h2, .typo h3, .typo h4, .typo h5, .typo h6,
.typo-h1, .typo-h2, .typo-h3, .typo-h4, .typo-h5, .typo-h6 {
    margin-top: 1.2em;
    margin-bottom: 0.6em;
    line-height: 1.35;
}

.typo h1, .typo-h1 {
    font-size: 2em;
}

.typo h2, .typo-h2 {
    font-size: 1.8em;
}

.typo h3, .typo-h3 {
    font-size: 1.6em;
}

.typo h4, .typo-h4 {
    font-size: 1.4em;
}

.typo h5, .typo h6, .typo-h5, .typo-h6 {
    font-size: 1.2em;
}

/*  ul  ol  */
.typo ul, .typo-ul {
    margin-left: 1.3em;
    list-style: disc;
}

.typo ol, .typo-ol {
    list-style: decimal;
    margin-left: 1.9em;
}

.typo li ul, .typo li ol, .typo-ul ul, .typo-ul ol, .typo-ol ul, .typo-ol ol {
    margin-bottom: 0.8em;
    margin-left: 2em;
}

.typo li ul, .typo-ul ul, .typo-ol ul {
    list-style: circle;
}

/*  ul/ol table  */
.typo table th, .typo table td, .typo-table th, .typo-table td, .typo table caption {
    border: 1px solid @gray-lighter;
    padding: 0.5em 1em;
    color: @gray-light;
}

.typo table th, .typo-table th {
    background: lighten(@gray-lighter, 10%);
}

.typo table thead th, .typo-table thead th {
    background: lighten(@gray-lighter, 10%);
}

.typo table caption {
    border-bottom: none;
}

/*  webkit  input  textarea   */
.typo-input, .typo-textarea {
    -webkit-appearance: none;
    border-radius: 0;
}

.typo-em, .typo em, legend, caption {
    color: #000;
    font-weight: inherit;
}

/* Responsive images */
.typo img {
    max-width: 100%;
    height: auto;
}