/* --------------------------------------------------------------
 * = ,  show,  hide
 * ----------------------------------------------------------- */

/*  */
@media screen and (max-width:35.5em) {
    .pure-show-md{display:none}
    .pure-show-lg{display:none}
    .pure-hide-sm{display:none}
    .pure-center-sm{text-align:center;}
}
/*  */
@media screen and (max-width:48em) {
    .pure-show-sm{display:none}
    .pure-show-lg{display:none}
    .pure-hide-md{display:none}
    .pure-center-md{text-align:center;}
}
/*  */
@media screen and (max-width:64em) {
    .pure-show-sm{display:none}
    .pure-show-md{display:none}
    .pure-hide-lg{display:none}
}
/*  */
@media screen and (min-width: 80em) {
    .pure-show-sm{display:none}
    .pure-show-md{display:none}
    .pure-hide-xl{display:none}
}